<template>


  <div class="wrapper" v-if="loader">
    <div ref="weight_data" class="weight-control" v-for="(record) in items" :key="record.id">
      <div class="date">
        <div class="date__current">
          {{ record.date }}
        </div>
        <div class="date__time">
          {{ record.time }}
        </div>
        <div class="date__day">
          {{ record.day }}
        </div>
      </div>
      <div class="weight">
        <span>{{ record.weight }} kg</span>
      </div>
      <div class="delete" @click="removeRecord(record.id)">
        <span>Удалить</span>
      </div>
    </div>
    <div style="margin: 0 auto;">
      <Paginate
          :page-count="pageCount"
          :click-handler="pageChangeHandler"
          :prev-text="'Назад'"
          :next-text="'Вперед'"
          :container-class="'pagination'"
          :page-class="'waves-effect'"
      />
    </div>

  </div>
</template>

<script>
import paginationMixin from "@/mixins/pagination.mixin"
import dateFilter from "../filters/date.filter";

export default {
  name: "YourWeight",
  data: () => ({
    date1: new Date,
    records: [],
    loader: true
  }),
  mixins: [paginationMixin],
  components: {
  },
  methods: {
    date(date) {
      let data = dateFilter(this.data, date)
      return data.toString()
    },
    async removeRecord(id) {
      await this.$store.dispatch("deleteRecord", id)
      this.$emit('change')
    }
  },

  async mounted() {
    try {
      this.records = await this.$store.dispatch('getValueFromDatabase')
      this.setupPagination(this.records.map(rec => {
        return {
          ...rec
        }
      }).reverse())
    } catch (e) {

    }
  }
}
</script>

<style scoped>
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>