<template>
  <div class="wrap">
    <div class="caption_change">
      <span class="caption_change__one">Дневник веса</span>
      <span class="caption_change__two">Введите новое значение веса</span>
    </div>
    <div class="change_weight">
      <form @submit.prevent="addData" class="change_weight__form">
        <div class="wrap_change">
          <div class="inputs__wrap">
            <input
                class="inputs"
                ref="input"
                type="text" v-model.trim="newWeight"
                @blur="$v.newWeight.$touch()"
                placeholder="Ваш вес на сегодня"
            >
            <small
                v-if="$v.newWeight.$dirty && !$v.newWeight.numbers"
            >
              Введите число в формате 00.0
            </small>
          </div>
          <button class="change_weight__btn" type="submit">Добавить</button>
        </div>
      </form>
    </div>
    <div class="change_weight__menu">
      <span class="change_weight__menu_item" :class="{active: show}" @click="show = true">Ваши записи</span>
      <span class="change_weight__menu_item" :class="{active: !show}" @click="show = false">График изменения веса</span>
    </div>

    <your-weight :key="key" v-if="show" @change="change"/>
    <div class="chart" v-else>
      <chart />
    </div>
  </div>
</template>

<script>
import dateFilter from "../filters/date.filter";
import Chart from '../components/Chart/chart'
import YourWeight from "../views/YourWeight";
import {required, helpers} from 'vuelidate/lib/validators'
const numbers = helpers.regex('numbers', /^[0-9]*[.]?[0-9]+$/)

export default {
  name: "ChangeData",
  data: () => ({
    newWeight: null,
    date: new Date,
    loader: true,
    key: 0,
    show: true
  }),
  validations: {
    newWeight: {
      required,
      numbers
    }
  },
  components: {
    YourWeight,
    Chart
  },
  methods: {
    addData() {
      if (this.newWeight) {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
        let date = dateFilter(this.date, 'date')
        let time = dateFilter(this.date, 'time')
        let day = dateFilter(this.date, 'day')
        const formData = {
          date,
          time,
          day,
          weight: this.newWeight
        }
        this.$store.dispatch('setValueToDatabase', formData)

        this.change()
        this.newWeight = ''
      }
    },
    change() {
      return this.key += 1
    }
  }
}
</script>

