<template>
    <canvas ref="canvas"></canvas>
</template>

<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  data() {
    return {
      gradient: null,
      gradient2: null,
      data: [],
      dataWeight: [],
      dataDate: [],
      wishWeight: null,
      wishWeightData: []
    };
  },
  async mounted() {
    const record = await this.$store.dispatch('getValueFromDatabase')
    this.data = record.map(rec => {return {...rec}}).reverse()
    this.dataWeight = this.data.map(rec => {return rec.weight}).reverse()
    this.dataDate = this.data.map(rec => {return rec.date}).reverse()
    this.wishWeight = await this.$store.dispatch('getOtherFromDatabase')
    this.wishWeight = this.wishWeight[1].wishWeight
    this.dataWeight.forEach(elem => {
      this.wishWeightData.push(this.wishWeight)
    })
    this.gradient = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);
    this.gradient2 = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);

    this.gradient.addColorStop(0, "rgba(255, 0,0, 0.5)");
    this.gradient.addColorStop(0.5, "rgba(255, 0, 0, 0.25)");
    this.gradient.addColorStop(1, "rgba(255, 0, 0, 0)");

    this.gradient2.addColorStop(0, "rgba(0, 231, 255, 0.9)");
    this.gradient2.addColorStop(0.5, "rgba(0, 231, 255, 0.25)");
    this.gradient2.addColorStop(1, "rgba(0, 231, 255, 0)");

    this.renderChart(
        {
          labels: this.dataDate,
          datasets: [
            {
              label: "Ваш вес",
              borderColor: "#FC2525",
              pointBackgroundColor: "red",
              borderWidth: 1,
              pointBorderColor: "white",
              backgroundColor: this.gradient,
              data: this.dataWeight
            },
            {
              label: "Желаемый вес",
              borderColor: "#05CBE1",
              pointBackgroundColor: "#05CBE1",
              pointBorderColor: "white",
              borderWidth: 1,
              backgroundColor: this.gradient2,
              data: this.wishWeightData

            }
          ]
        },
        { responsive: true, maintainAspectRatio: false }
    );
  }
};
</script>

